import React, { useState } from 'react';
import { Typography, Button, Form, Input } from 'antd';
import FileUpload from '../../utils/FileUpload';
import axios from 'axios';

const { Title } = Typography;
const { TextArea } = Input;
const Continents = [
    { key: 1, value: 'Africa' },
    { key: 2, value: 'Europe' },
    { key: 3, value: 'Asia' },
    { key: 4, value: 'North America' },
    { key: 5, value: 'South America' },
    { key: 6, value: 'Australia' },
    { key: 7, value: 'Antarctica' },
];

function UploadProductPage({ user, history }) {
    const [ProductTitle, setProductTitle] = useState('');
    const [Description, setDescription] = useState('');
    const [Price, setPrice] = useState(0);
    const [Continent, setContinent] = useState(1);
    const [Images, setImages] = useState([]);
    const titleChangeHandler = (e) => {
        setProductTitle(e.currentTarget.value);
    };

    const descriptionChangeHandler = (e) => {
        setDescription(e.currentTarget.value);
    };

    const priceChangeHandler = (e) => {
        setPrice(e.currentTarget.value);
    };

    const continentChangeHandler = (e) => {
        setContinent(e.currentTarget.value);
    };

    const updateImages = (newImages) => {
        setImages(newImages);
    };

    const submitHandler = (e) => {
        e.preventDefault();
        //  
        if (!ProductTitle || !Description || !Price || !Continent || !Images) {
            return alert('    ~ !');
        }

        // request   Server  
        const body = {
            writer: user.userData._id,
            title: ProductTitle,
            description: Description,
            price: Price,
            images: Images,
            continents: Continent,
        };
        axios.post('/api/product', body).then((response) => {
            if (response.data.success) {
                alert('   .');
                history.push('/product');
            } else {
                alert('   ');
            }
        });
    };

    return (
        <div style={{ maxWidth: '700px', margin: '2rem auto' }}>
            <div style={{ textAlign: 'center', marginBottom: '2rem' }}>
                <Title level={2} style={{ letterSpacing: '0.15rem' }}>
                    Package Tour Upload
                </Title>
            </div>
            <Form onSubmit={submitHandler}>
                <FileUpload refreshFunction={updateImages} />
                <br />
                <br />
                <label></label>
                <Input onChange={titleChangeHandler} value={ProductTitle} />
                <br />
                <br />
                <label></label>
                <TextArea onChange={descriptionChangeHandler} value={Description} />
                <br />
                <br />
                <label>($)</label>
                <Input type="number" onChange={priceChangeHandler} value={Price} />
                <br />
                <br />
                <select onChange={continentChangeHandler} value={Continent}>
                    {Continents.map((item) => (
                        <option key={item.key} value={item.key}>
                            {item.value}
                        </option>
                    ))}
                </select>
                <br />
                <br />
                <Button onClick={submitHandler}> </Button>
            </Form>
        </div>
    );
}

export default UploadProductPage;
